import { Box, Button, Container, Stack, Typography } from '@mui/material'
import React from 'react'

import { theme } from '../../helper/theme'
import bg from '../../images/forest.png'

const bigTextStyle = {
    fontSize: '32px',
    color: '#fff',
    width: "513px"

}

const smallTextStyle = {
    fontSize: '18px',
    color: '#fff'
}

const Hero = () => {
    return (
        <>
            <Container fixed sx={{ padding: "60px", backgroundImage:`url(${bg})` }}>
                <Stack direction={'row'} sx={{ height: "580px" }}>
                    <Box sx={{ bgcolor: theme.palette.primary.dark, width: "60%", height: '100%', p: 6 }}>
                        <Stack justifyContent={'space-between'} sx={{ height: '100%' }}>
                            <Stack alignItems={'flex-start'} spacing={4}>
                                <Typography sx={bigTextStyle}>    The furniture brand for the future, with timeless designs</Typography>
                                <Button variant='conatained' sx={smallTextStyle}>View Collection</Button>
                            </Stack>
                            <Typography sx={smallTextStyle}>
                                A new era in eco friendly furniture with Avelon, the French luxury retail brand
                                with nice fonts, tasteful colors and a beautiful way to display things digitally
                                using modern web technologies.
                            </Typography>
                        </Stack>
                     
                    </Box>
                   

                </Stack>
            
            </Container>
        </>
    )
}

export default Hero
