import { Box, Card, CardActionArea, CardContent, Container, Grid, Typography } from '@mui/material'
import React from 'react'
import { componenetTitle } from '../../helper/style'

const cards = [
    {
        id: 1,
        name: 'The Dandy chair',
        price: '250'
    },
    {
        id: 2,
        name: 'Rustic Vase Set',
        price: '150'
    },
    {
        id: 3,
        name: 'The Silky Vase',
        price: '125'
    },
    {
        id: 3,
        name: 'The Lucy Lamp',
        price: '125'
    }

]

const Ceramics = () => {
    return (
        <>
            <Container fixed sx={{ pt: 8, pb: 8 }} >
                <Typography sx={componenetTitle}>New Ceramics</Typography>
                <Grid container spacing={3}>

                    {cards.map((el, i) => {
                        return (
                            <Grid item lg={3} md={3} sm={6} key={i}>
                                <Card sx={{ height: '100px' }}>
                                    <CardActionArea>
                                        <CardContent>
                                            <Box sx={{bgcolor:'red', height:'200px', width:'100%'}}></Box>
                                            <Typography>{el.name}</Typography>
                                            <Typography>{el.price}</Typography>
                                        </CardContent>
                                    </CardActionArea>
                                </Card>
                            </Grid>
                        )
                    })}

                    {/* <Grid item lg={3} md={3} sm={6}>
                        <Card sx={{ height: '100px' }}>
                            <CardActionArea>
                                <CardContent>
                                    <Typography>sfdddddd</Typography>
                                </CardContent>
                            </CardActionArea>
                        </Card>
                    </Grid>
                    <Grid item lg={3} md={3} sm={6}>
                        <Card sx={{ height: '100px' }}>
                            <CardActionArea>
                                <CardContent>
                                    <Typography>sfdddddd</Typography>
                                </CardContent>
                            </CardActionArea>
                        </Card>
                    </Grid>
                    <Grid item lg={3} md={3} sm={6}>
                        <Card sx={{ height: '100px' }}>
                            <CardActionArea>
                                <CardContent>
                                    <Typography>sfdddddd</Typography>
                                </CardContent>
                            </CardActionArea>
                        </Card>
                    </Grid> */}
                </Grid>
            </Container>
        </>
    )
}

export default Ceramics
